import React, {useCallback, useState} from "react";
import ReserveP from "./ReserveP";
import {useDispatch} from "react-redux";
import {reserve} from "../../modules/reserve";

export default () => {
    const [state, setState] = useState<IReserve>({
        field: 0,
        date: null,
        name: '',
        phone: '',
        place: 0,
        title: '',
        content: ''
    });

    const dispatch = useDispatch();
    const reservation = useCallback((state)=>{dispatch(reserve(state))},[dispatch]);

    return <ReserveP state={state} setState={setState} reserve={reservation}/>
}
